import React, { useEffect, useState } from 'react'
import {
    Container,
    Busca,
    ResultadoBusca,
    TitleResultadoBusca,
    DataBusca,
    NoDataBusca,
    InfoProduct,
    Estoque,
} from "./ProdutosElements"
import { Layout } from "../Layout/Index"
import { Row, Col } from "react-bootstrap"
import martelo from "../../img/martelo.png"
import { IoIosSave } from "react-icons/io"
import { BsPencilSquare } from "react-icons/bs"
import { MdDelete } from "react-icons/md"
import { TiDelete } from "react-icons/ti"
import { DataProdutos } from "./DataProdutos"
import { MdCloudUpload } from "react-icons/md"
import { BiErrorAlt } from "react-icons/bi"

export const Produto = ({ isOpen }) => {

    const [filtroBusca, setFiltroBusca] = useState([]);
    const [valorDigitado, setValorDigitado] = useState("");

    function moneyFormatter(money) {
        const valor = new Intl.NumberFormat('pt-BR',
            { style: 'currency', currency: 'BRL' }
        ).format(money);

        return valor;

        // função formata valor numérico para R$ (moeda brasileira) e retorna valor formatado
    }

    useEffect(() => {
        setFiltroBusca( // filtra todos os dados de acordo com as condições dos inputs da variavél (data)
            DataProdutos.filter(item => { //armazena cada item na variável - state (FilteredData)
                return item.produto.toString().toLowerCase().indexOf(valorDigitado.toLowerCase()) > -1
            })
        )
    }, [valorDigitado]) // executa toda vez que algum dos <= states é alterado 


    // const saveProduto = () => {

    //     if (document.getElementById("produto_name") != null) {
    //         let produto = document.getElementById("produto_name").value;

    //         DataProdutos.push(
    //             {
    //                 item: 200, 
    //                 cod_barras: "teste", 
    //                 produto: "teste", 
    //                 quantidade: "teste", 
    //                 kit: "teste",
    //                 und: "teste",
    //                 numero_serie: "teste",
    //                 localizacao: "teste",
    //                 custo: 12324960
    //             })
    //     }
    // }


    return (
        <Container isOpen={isOpen}>
            <Busca>

                <div className="wrap-filtros">

                    <div className="box-filtros">
                        <input type="checkbox" id="produtos" name="produtos" />
                        <label for="produtos">Produtos</label>
                    </div>

                    <div className="box-filtros">
                        <input type="checkbox" id="codigo" name="codigo" />
                        <label for="codigo">Código</label>
                    </div>

                    <div className="box-filtros">
                        <input type="checkbox" id="num_serie" name="num_serie" />
                        <label for="num_serie">Nº Série</label>
                    </div>

                    <div className="box-filtros">
                        <input type="checkbox" id="localization" name="localization" />
                        <label for="localization">Localização</label>
                    </div>

                </div>

                <div className="box-busca">
                    <input type="search" id="busca" name="busca" placeholder="Pesquisar por produto..." value={valorDigitado} onChange={((e) => setValorDigitado(e.target.value))} />
                </div>
            </Busca>

            <ResultadoBusca>

                <TitleResultadoBusca>
                    <div className="container" style={{ maxWidth: '100%' }}>
                        <Row style={{ margin: 0, maxWidth: "100%" }}>
                            <Col xs={1} className="coluna-titulo-busca">
                                <p>Item</p>
                            </Col>

                            <Col xs={2} className="coluna-titulo-busca">
                                <p>Código de Barras</p>
                            </Col>

                            <Col xs={2} className="coluna-titulo-busca">
                                <p>Produto</p>
                            </Col>

                            <Col xs={1} className="coluna-titulo-busca">
                                <p>Qtde</p>
                            </Col>

                            <Col xs={1} className="coluna-titulo-busca">
                                <p>Kit</p>
                            </Col>

                            <Col xs={1} className="coluna-titulo-busca">
                                <p>UND</p>
                            </Col>

                            <Col xs={1} className="coluna-titulo-busca">
                                <p>Nº Série</p>
                            </Col>

                            <Col xs={2} className="coluna-titulo-busca">
                                <p>Localização</p>
                            </Col>

                            <Col xs={1} className="coluna-titulo-busca">
                                <p>Custo</p>
                            </Col>

                        </Row>
                    </div>

                </TitleResultadoBusca>


                {filtroBusca.length > 0 ? filtroBusca.map((item) => {
                    return (
                        <DataBusca onClick={console.log("teste")}>
                            <div className="container" style={{ maxWidth: '100%' }}>
                                <Row style={{ margin: 0, maxWidth: "100%" }}>
                                    <Col xs={1} className="coluna-resultado-busca">
                                        <p>{item.item}</p>
                                    </Col>

                                    <Col xs={2} className="coluna-resultado-busca">
                                        <p>{item.cod_barras}</p>
                                    </Col>

                                    <Col xs={2} className="coluna-resultado-busca">
                                        <p style={{ textTransform: 'uppercase' }}>{item.produto}</p>
                                    </Col>

                                    <Col xs={1} className="coluna-resultado-busca">
                                        <p>{item.quantidade}</p>
                                    </Col>

                                    <Col xs={1} className="coluna-resultado-busca">
                                        <p style={{ textTransform: 'uppercase' }}>{item.kit}</p>
                                    </Col>

                                    <Col xs={1} className="coluna-resultado-busca">
                                        <p style={{ textTransform: 'uppercase' }}>{item.und}</p>
                                    </Col>

                                    <Col xs={1} className="coluna-resultado-busca">
                                        <p>{item.numero_serie}</p>
                                    </Col>

                                    <Col xs={2} className="coluna-resultado-busca">
                                        <p>{item.localizacao}</p>
                                    </Col>

                                    <Col xs={1} className="coluna-resultado-busca">
                                        <p>{moneyFormatter(item.custo)}</p>
                                    </Col>

                                </Row>
                            </div>
                        </DataBusca>

                    )
                }) :
                    <NoDataBusca>
                        <div className="no-data-message">
                            <p><BiErrorAlt className="icone-no-data" /> Nenhum produto encontrado <BiErrorAlt className="icone-no-data" /></p>
                        </div>
                    </NoDataBusca>
                }

            </ResultadoBusca>

            <InfoProduct>
                <Row>
                    <Col xs={12}>
                        <Row >
                            <Col xs={9} className="coluna-inputs">

                                <Row >
                                    <Col xs={12} className="coluna-inputs">
                                        <div className="coluna-inputs-item">
                                            <label for="cod_barras">Código de barras:</label>
                                            <input type="text" id="cod_barras" />
                                        </div>

                                        <div className="coluna-inputs-item">
                                            <label for="produto_name">Produto:</label>
                                            <input type="text" id="produto_name" />
                                        </div>

                                        <div className="coluna-inputs-item">
                                            <label for="qtde">Quantidade:</label>
                                            <input type="text" id="cod_barras" />
                                        </div>

                                        <div className="coluna-inputs-item">
                                            <label for="cod_barras">Marca Modelo</label>
                                            <input type="text" id="cod_barras" />
                                        </div>

                                        <div className="coluna-inputs-item">
                                            <label for="cod_barras">Unid. Medida</label>
                                            <input type="text" id="cod_barras" />
                                        </div>
                                    </Col>

                                    <Col xs={12} className="coluna-inputs">

                                        <div className="coluna-inputs-item">
                                            <label for="cars">Categoria:</label>

                                            <select name="cars" id="cars">
                                                <option value="volvo">Categoria 1</option>
                                                <option value="saab">Categoria 2</option>
                                                <option value="mercedes">Categoria 3</option>
                                                <option value="audi">Categoria 4</option>
                                            </select>
                                        </div>

                                        <div className="coluna-inputs-item">
                                            <label for="cod_barras">Localização:</label>
                                            <input type="text" id="cod_barras" />
                                        </div>

                                        <div className="coluna-inputs-item">
                                            <label for="cod_barras">Custo Produto:</label>
                                            <input type="text" id="cod_barras" />
                                        </div>

                                    </Col>

                                    <Col xs={12} className="coluna-inputs">

                                        <div className="coluna-inputs-item">
                                            <label for="cod_barras">Nº de Série:</label>
                                            <input type="text" id="cod_barras" />
                                        </div>

                                        <div className="coluna-inputs-item">
                                            <label for="data_compra">Mais informações</label>
                                            <input type="text" id="cod_barras" />
                                        </div>

                                    </Col>

                                    <Col xs={12} className="coluna-inputs">

                                        <div className="coluna-inputs-item">
                                            <label for="data_compra">Data da Compra:</label>
                                            <input type="datetime-local" id="data_picker" className="data_picker"
                                                name="meeting-time" />
                                        </div>

                                        <div className="coluna-inputs-item">
                                            <label for="cod_barras">Data Frabricação:</label>
                                            <input type="datetime-local" id="data_picker" className="data_picker"
                                                name="meeting-time" />
                                        </div>

                                        <div className="coluna-inputs-item">
                                            <label for="cod_barras">Data Validade:</label>


                                            <input type="datetime-local" id="data_picker" className="data_picker"
                                                name="meeting-time" />
                                        </div>

                                    </Col>

                                </Row>



                            </Col>

                            <Col xs={3} className="coluna-img">
                                <img src={martelo} alt="martelo" />
                                <button type="button">Importar<MdCloudUpload className="icone-importar-img" /></button>
                            </Col>
                        </Row>

                    </Col>

                    <Col xs={12} className="coluna-inputs">
                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Fornecedor:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="produto">Fabricante:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="qtde">Quantidade:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Marca Modelo</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Unid. Medida</label>
                            <input type="text" id="cod_barras" />
                        </div>

                    </Col>


                    <Col xs={12} className="coluna-inputs">
                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Fornecedor:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="produto">Fabricante:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="qtde">Quantidade:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Marca Modelo</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Unid. Medida</label>
                            <input type="text" id="cod_barras" />
                        </div>

                    </Col>

                    <Col xs={12} className="coluna-inputs">
                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Fornecedor:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="produto">Fabricante:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="qtde">Quantidade:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Marca Modelo</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Unid. Medida</label>
                            <input type="text" id="cod_barras" />
                        </div>

                    </Col>

                    <Col xs={12} className="coluna-inputs">
                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Fornecedor:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="produto">Fabricante:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="qtde">Quantidade:</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Marca Modelo</label>
                            <input type="text" id="cod_barras" />
                        </div>

                        <div className="coluna-inputs-item">
                            <label for="cod_barras">Unid. Medida</label>
                            <input type="text" id="cod_barras" />
                        </div>

                    </Col>
                </Row>

            </InfoProduct>

            <Estoque>
                <Layout>
                    <Row>

                        <Col className="wrap-box" xs={10}>
                            <h6>Resumo do Estoque</h6>

                            <div className="box-info">

                                <div className="coluna-inputs-item">
                                    <label for="data_compra">Estoque Atual</label>
                                    <input type="text" id="cod_barras" />
                                </div>

                                <div className="coluna-inputs-item">
                                    <p>+</p>
                                </div>

                                <div className="coluna-inputs-item">
                                    <label for="data_compra">Compras</label>
                                    <input type="text" id="cod_barras" />
                                </div>

                                <div className="coluna-inputs-item">
                                    <p>=</p>
                                </div>

                                <div className="coluna-inputs-item">
                                    <label for="data_compra">Estoque Final</label>
                                    <input type="text" id="cod_barras" />
                                </div>
                            </div>
                        </Col>

                        <Col xs={2} className="wrap-box" style={{ border: "none" }} >
                            <div className="btn-estoque">
                                <button type="button">Cadastrar<IoIosSave className="icone-btn" style={{ color: "#008000" }} /></button>
                                <button type="button">Alterar<BsPencilSquare className="icone-btn" style={{ color: "#0000FF" }} /></button>
                                <button type="button">Excluir<MdDelete className="icone-btn" style={{ color: "#FF0000" }} /></button>
                                <button type="button">Limpar<TiDelete className="icone-btn" style={{ color: "#FFA500" }} /></button>
                            </div>
                        </Col>

                    </Row>
                </Layout>
            </Estoque>



        </Container>
    )
}
